$grey_color: rgb(102, 102, 102);
$red_color: #d68686;

* {
  transition: 0.3s; }

.blog__nav {
  background: rgba(120, 120, 120, 1);
  color: white;
  padding: 15px;
  position: fixed;
  display: inline-block;
  width: 100%;
  padding-left: 20px;
  z-index: 2; }

.blog__nav-name {
  cursor: pointer;
  font-size: 24px;
  color: white;
  &:hover {
    color: rgba(255, 255, 255, 0.9); } }

[class^='blog__nav-']:not(.blog__nav-name) {
  color: white;
  font-size: 20px;
  position: absolute;
  bottom: 15px;
  &:hover {
    color: rgba(255, 255, 255, 0.9); } }

.blog__nav-about {
  right: 190px;
  z-index: 0; }

.blog__nav-list {
  right: 120px;
  margin-right: 10px; }

.blog__nav-addpost {
  right: 15px;
  margin-right: 10px; }

[class^='blog__nav'] {
  &:hover {
    text-decoration: none; } }

[class^='blog__nav-']:not(.blog__nav-name) {
  cursor: pointer;
  &:hover {
    color: rgba(255, 255, 255, 0.9); } }

.blog__list {
  margin-left: 50%;
  transform: translateX(-50%);
  margin-top: -13%;
  padding: 0px 20px; }

.blog__list-img {
  width: 100%; }

.blog__list-title {
  font-size: 24px;
  text-align: center;
  color: $grey_color;
  font-weight: bold;
  margin-bottom: 30px; }

.fa-list-ul {
  margin-right: 10px; }

.blog__list-aritcle {
  margin-top: 10px;
  padding: 10px;
  background: $red_color;
  cursor: pointer;
  &:hover {
    background: #F1BCBC; }
  &:nth-last-child(1) {
    margin-bottom: 30px; } }

.blog__article-img {
  width: 100%; }

.blog__article {
  margin-left: 50%;
  transform: translateX(-50%);
  color: $grey_color;
  background-color: white; }

.blog__article-title {
  font-weight: bold;
  font-size: 30px;
  margin: 30px 0px; }

.blog__article-action {
  display: flex;
  padding-right: 10px;
  margin: 30px 0px;
  justify-content: flex-end; }

.blog__article-edit, .blog__article-delete {
  border: none;
  color: $grey_color;
  &:focus {
    outline: none; }
  &:hover {
    color: lighten($grey_color, 10); } }

.blog__article-delete {
  margin-left: 20px; }

.fa-edit, .fa-trash-alt {
  margin-right: 5px; }

.blog__article-author {
  color: $red_color;
  margin-bottom: 40px;
  padding-left: 10px; }

.blog__article-body {
  margin-bottom: 60px; }

// 編輯文章
[class^='blog__article-edit']:not(.blog__article-editsubmit):not(.blog__article-edit):not(.blog__article-editbody) {
  margin-bottom: 20px;
  border: none;
  border-bottom: solid 1px lighten($grey_color, 30);
  width: 100%;
  outline: none;
  padding: 8px 8px;
  margin-top: 5px;
  &:focus {
    border-bottom: solid 2px $red_color; } }

.blog__article-editbody, .blog__addpost-body {
  margin-top: 5px;
  padding: 8px 8px;
  outline: none;
  width: 100%;
  margin-bottom: 20px;
  height: 200px;
  border: solid 1px lighten($grey_color, 30);
  &:focus {
    border: solid 2px $red_color; } }

.blog__article-editsubmit, .blog__addpost-submit {
  margin-left: 50%;
  transform: translateX(-50%);
  border: solid 1px lighten($grey_color, 30);
  margin-bottom: 30px;
  padding: 5px 15px;
  &:hover {
    background-color: $red_color;
    color: white;
    border: solid 1px $red_color; }
  &:focus {
    outline: none; } }

.blog__article-action>a.blog__article-canceledit {
  cursor: pointer;
  font-size: 14px;
  &:hover {
    color: $red_color; } }

.fa-user {
  margin-right: 10px; }

.blog__about-img {
  height: 100vh;
  width: 50%; }

.blog__about {
  display: flex; }

.blog__about-content {
  text-vertical: center;
  width: 50%;
  text-align: center;
  line-height: 100px;
  margin: auto;
  color: $grey_color; }

.fa-user-alt {
  border: solid 2px $grey_color;
  border-radius: 50%;
  padding: 20px;
  font-size: 50px;
  color: $grey_color;
  text-align: center;
  margin-left: 50%;
  transform: translateX(-50%);
  margin-top: 30px;
  margin-bottom: 20px; }

.blog__about-title {
  text-align: center;
  margin-bottom: 20px; }

.blog__list-body {
  border: solid 1px rgba(102, 102, 102, 0.3);
  max-width: 800px;
  margin-bottom: 30px;
  height: 180px;
  position: relative;
  cursor: pointer;
  &:hover {
    border-color: rgba(102, 102, 102, 1); } }

.blog__list-body-title, .blog__list-body-content, .blog__list-readmore {
  color: $grey_color; }

.blog__list-body-title {
  font-weight: bold;
  padding: 20px;
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.blog__list-body-line {
  border: solid 2px $red_color;
  width: 50px;
  position: absolute;
  left: 20px;
  border-radius: 10%; }

.blog__list-body-content {
  padding: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 200; }

.blog__list-readmore {
  margin-left: 20px;
  cursor: pointer;
  display: inline-block;
  &:hover {
    color: $red_color; } }

// AddPost
.blog__addpost {
  position: absolute;
  top: 100px;
  left: 50%;
  width: 100%;
  padding: 0px 100px;
  transform: translateX(-50%);
  & input {
    padding: 20px; } }

.blog__addpost-title {
  font-size: 24px;
  text-align: center;
  color: $grey_color;
  font-weight: bold;
  margin-bottom: 40px; }

.fa-plus-square {
  margin-right: 10px; }

[class^='blog__addpost-']:not(.blog__addpost-title):not(.blog__addpost-submit):not(.blog__addpost-body) {
  width: 100%;
  margin-bottom: 20px;
  border: none;
  border-bottom: solid 1px lighten($grey_color, 30);
  width: 100%;
  outline: none;
  padding: 8px 8px;
  margin-top: 5px;
  &:focus {
    border-bottom: solid 2px $red_color; }
  &::placeholder {
    color: lighten($grey_color, 30); } }

.blog__addpost-body {
  &::placeholder {
    color: lighten($grey_color, 30); } }

@media screen and (max-width: 1000px) {
  .blog__about {
    display: inline-block; }

  .blog__about-img {
    width: 100%;
    height: 50vh; }

  .blog__about-content {
    text-align: center;
    margin-top: 50px; } }

@media screen and (max-width: 890px) {
  .blog__list {
    margin-left: 0%;
    transform: translateX(0%); } }

@media screen and (max-width: 750px) {
  .blog__article {
    padding: 0px 20px;
    margin-left: 0%;
    transform: translateX(0%); } }
